<script>
import { ref, computed, onMounted } from 'vue';
import { useRouter } from 'vue-router';
import { auth } from '../firebase';
import { formatCurrency } from '../utils/taxUtils';
import Settings from '../views/Settings.vue';

export default {
  name: 'UserProfile',
  components: {
    Settings
  },
  setup() {
    const router = useRouter();
    const loading = ref(true);
    const activeTab = ref('saved');
    const userEmail = ref('');
    const userName = ref('');
    
    // Sample data - in a real app, these would come from your database
    const savedItems = ref([]);
    const billingHistory = ref([]);
    const proposals = ref([]);

    const userInitials = computed(() => {
      if (userName.value) {
        return userName.value
          .split(' ')
          .map(name => name.charAt(0))
          .join('')
          .toUpperCase();
      }
      if (userEmail.value) {
        return userEmail.value.charAt(0).toUpperCase();
      }
      return 'U';
    });
    
    const logout = async () => {
      try {
        await auth.signOut();
        router.push('/auth');
      } catch (error) {
        console.error('Error during logout:', error);
      }
    };

    const formatDate = (date) => {
      if (!date) return 'N/A';
      
      if (typeof date === 'string') {
        date = new Date(date);
      }
      
      if (date instanceof Date && !isNaN(date)) {
        return date.toLocaleDateString('en-US', {
          year: 'numeric',
          month: 'short',
          day: 'numeric'
        });
      }
      
      return 'N/A';
    };

    const getItemTypeClasses = (type) => {
      switch (type) {
        case 'tax':
          return 'bg-green-100 rounded-lg text-green-700';
        case 'invoice':
          return 'bg-blue-100 rounded-lg text-blue-700';
        case 'proposal':
          return 'bg-purple-100 rounded-lg text-purple-700';
        default:
          return 'bg-gray-100 rounded-lg text-gray-700';
      }
    };

    const getItemTypeLabel = (type) => {
      switch (type) {
        case 'tax':
          return 'Tax Calculation';
        case 'invoice':
          return 'Invoice';
        case 'proposal':
          return 'Proposal';
        default:
          return 'Item';
      }
    };

    const getDefaultTitle = (item) => {
      switch (item.type) {
        case 'tax':
          return `Tax Calculation - ${formatCurrency(item.grossIncome)}`;
        case 'invoice':
          return `Invoice ${item.invoiceNumber || ''}`;
        case 'proposal':
          return `Proposal for ${item.clientName || 'Client'}`;
        default:
          return 'Untitled Item';
      }
    };

    const getStatusClasses = (status) => {
      status = status?.toLowerCase() || '';
      
      switch (status) {
        case 'paid':
          return 'bg-green-100 text-green-800';
        case 'pending':
          return 'bg-yellow-100 text-yellow-800';
        case 'overdue':
          return 'bg-red-100 text-red-800';
        case 'draft':
          return 'bg-gray-100 text-gray-800';
        case 'accepted':
          return 'bg-blue-100 text-blue-800';
        case 'rejected':
          return 'bg-red-100 text-red-800';
        default:
          return 'bg-gray-100 text-gray-800';
      }
    };

    const viewItem = (item) => {
      switch (item.type) {
        case 'tax':
          router.push('/tax-calculator');
          break;
        case 'invoice':
          router.push('/billing');
          break;
        case 'proposal':
          router.push('/proposal');
          break;
        default:
          console.log('Viewing item:', item);
      }
    };

    const deleteItem = (item) => {
      // Implementation would delete the item from your database
      console.log('Deleting item:', item);
      
      if (item.type === 'tax') {
        savedItems.value = savedItems.value.filter(i => i.id !== item.id);
      } else if (item.type === 'invoice') {
        billingHistory.value = billingHistory.value.filter(i => i.id !== item.id);
      } else if (item.type === 'proposal') {
        proposals.value = proposals.value.filter(i => i.id !== item.id);
      }
    };

    const viewInvoice = (invoice) => {
      router.push('/billing');
      console.log('Viewing invoice:', invoice);
    };

    const downloadInvoice = (invoice) => {
      console.log('Downloading invoice:', invoice);
    };

    const viewProposal = (proposal) => {
      router.push('/proposal');
      console.log('Viewing proposal:', proposal);
    };
    
    const loadUserData = async () => {
      loading.value = true;
      try {
        const user = auth.currentUser;
        if (user) {
          userEmail.value = user.email;
          userName.value = user.displayName || '';
          
          // In a real app, you would fetch user data from Firestore here
          // For this example, we'll use placeholder data
          savedItems.value = [
            {
              id: '1',
              type: 'tax',
              date: new Date(),
              grossIncome: 1200000,
              netIncome: 960000,
              title: 'Annual Tax Calculation 2023'
            },
            {
              id: '2',
              type: 'invoice',
              date: new Date(Date.now() - 7 * 24 * 60 * 60 * 1000), // 7 days ago
              invoiceNumber: 'INV-2023-001',
              clientName: 'Acme Inc.',
              totalAmount: 45000,
              status: 'Paid'
            },
            {
              id: '3',
              type: 'proposal',
              date: new Date(Date.now() - 14 * 24 * 60 * 60 * 1000), // 14 days ago
              clientName: 'TechStart Ltd',
              projectValue: 120000,
              title: 'Website Redesign Proposal'
            }
          ];
          
          billingHistory.value = [
            {
              id: '1',
              invoiceNumber: 'INV-2023-001',
              clientName: 'Acme Inc.',
              date: new Date(),
              totalAmount: 45000,
              status: 'Paid'
            },
            {
              id: '2',
              invoiceNumber: 'INV-2023-002',
              clientName: 'TechStart Ltd',
              date: new Date(Date.now() - 15 * 24 * 60 * 60 * 1000),
              totalAmount: 75000,
              status: 'Pending'
            },
            {
              id: '3',
              invoiceNumber: 'INV-2023-003',
              clientName: 'Creative Solutions',
              date: new Date(Date.now() - 45 * 24 * 60 * 60 * 1000),
              totalAmount: 36000,
              status: 'Overdue'
            }
          ];
          
          proposals.value = [
            {
              id: '1',
              title: 'Website Redesign',
              clientName: 'TechStart Ltd',
              date: new Date(Date.now() - 14 * 24 * 60 * 60 * 1000),
              projectValue: 120000,
              status: 'Accepted'
            },
            {
              id: '2',
              title: 'Brand Identity Package',
              clientName: 'Fashion Forward',
              date: new Date(Date.now() - 5 * 24 * 60 * 60 * 1000),
              projectValue: 85000,
              status: 'Pending'
            }
          ];
        }
      } catch (error) {
        console.error('Error loading user data:', error);
      } finally {
        loading.value = false;
      }
    };

    onMounted(async () => {
      await loadUserData();
    });

    return {
      activeTab,
      userEmail,
      userName,
      loading,
      savedItems,
      billingHistory,
      proposals,
      userInitials,
      logout,
      formatCurrency,
      formatDate,
      getItemTypeClasses,
      getItemTypeLabel,
      getDefaultTitle,
      getStatusClasses,
      viewItem,
      deleteItem,
      viewInvoice,
      downloadInvoice,
      viewProposal
    };
  }
};
</script>

